#wrapper {
  margin: 0;
  padding: 0; }

#site-header {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative; }
  #site-header:after {
    content: " ";
    display: block;
    clear: both; }
  head {
    display: block;
    position: fixed;
    right: 10px;
    bottom: 10px;
    z-index: 999;
    color: #333;
    background: rgba(255, 255, 255, 0.25); }
  head:before {
    content: "|||";
    display: block;
    padding: 5px 10px;
    font-family: sans-serif;
    font-size: 16px;
    font-weight: bold; }
  head:hover {
    background: rgba(255, 255, 255, 0.5);
    color: red; }
  head:hover ~ #site-header,
  head:hover ~ body #site-header {
    position: relative; }
    head:hover ~ #site-header:before,
    head:hover ~ body #site-header:before {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      content: " ";
      z-index: 998;
      background-image: linear-gradient(to right, rgba(102, 102, 255, 0.15), rgba(179, 179, 255, 0.15) 80%, transparent 80%);
      background-size: 8.47458%;
      background-origin: content-box;
      background-clip: content-box;
      background-position: left top; }

